.landingpage {
  width: 100%;
  background-color: rgb(236, 236, 236);
}
.backgroundImage_container {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: relative;
}

.home_image {
  min-height: 150px;
  width: 100%;
  z-index: -1;
  mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
}
.Container {
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 1;
  display: flex;
  flex-direction: column;
}
.slider {
  height: 40%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 1px;
}
.leftSlider {
  height: 105%;
  width: 5%;
  margin-top: 1%;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 2px solid rgba(0, 0, 0, 0);
}
.leftSliderin {
  height: 98%;
  width: 98%;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 2px solid rgba(0, 0, 0, 0);
}
.rightSliderin {
  height: 98%;
  width: 98%;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 2px solid rgba(0, 0, 0, 0);
}
.leftSlider:hover,
.rightSlider:hover {
  cursor: pointer;
  border: 2px solid rgb(255, 255, 255);
}
.leftSliderin:hover,
.rightSliderin:hover {
  border: 2px solid rgb(0, 142, 207);
}
.arrows {
  color: white;
  font-size: 40px;
}

.rightSlider {
  height: 105%;
  width: 5%;
  margin-top: 1%;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 2px solid rgba(0, 0, 0, 0);
}

.firstItemContainer {
  height: 100%;
  display: flex;
  justify-content: space-evenly;
}
.langingpageItems {
  margin-top: 1%;
  height: 100%;
  width: 97.5%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.landingpageCatagory {
  height: 90%;
  width: 30%;
  background-color: rgb(255, 255, 255);
  display: flex;
  flex-direction: column;
  align-items: center;
  cursor: pointer;
}
.landingpageCatagory h2 {
  font-size: 2vw;
  max-width: 60%;
  margin-top: 0;
  margin-bottom: 2%;
}
.landingpageCatagory img {
  height: 80%;
  width: 85%;
}
